<template>
  <el-container style="height: 100%">
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column label="User" align="center" prop="name" sortable>
        <template #default="scope">
          <el-link @click="this.$router.push(`/user/${scope.row.name}`)">
            {{ scope.row.name }}
          </el-link>
        </template>
      </el-table-column>
      <el-table-column
        label="Codeforces"
        align="center"
        prop="codeforces_handle"
        sortable
      >
        <template #default="scope">
          <el-link
            type="primary"
            :href="`https://codeforces.com/profile/${scope.row.codeforces_handle}`"
            target="_blank"
            :underline="false"
          >
            <el-avatar
              v-show="scope.row.codeforces_avatar"
              :size="28"
              :src="scope.row.codeforces_avatar"
              style="margin-right: 6px"
            />
            {{ scope.row.codeforces_handle }}</el-link
          >
        </template>
      </el-table-column>
      <el-table-column
        label="LeetCode"
        align="center"
        prop="leetcode_handle"
        sortable
      >
        <template #default="scope">
          <el-link
            type="primary"
            :href="`https://leetcode.cn/u/${scope.row.leetcode_handle}/`"
            target="_blank"
            :underline="false"
          >
            <el-avatar
              v-show="scope.row.leetcode_avatar"
              :size="28"
              :src="scope.row.leetcode_avatar"
              style="margin-right: 6px"
            />{{ scope.row.leetcode_handle }}</el-link
          >
        </template>
      </el-table-column>
      <el-table-column label="Operation" width="150px" align="center">
        <template #default="scope">
          <el-button
            size="small"
            type="danger"
            @click="unfollow(scope.row.name)"
            >Delete</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-footer height="60px" style="padding: 10px">
      <el-form
        :inline="true"
        :model="followingToAdd"
        :rules="rules"
        ref="formRef"
        style="text-align: center"
      >
        <el-form-item label="User" prop="name">
          <el-input v-model="followingToAdd.name" />
        </el-form-item>
        <el-form-item label="Codeforces" prop="codeforces_handle">
          <el-input v-model="followingToAdd.codeforces_handle" />
        </el-form-item>
        <el-form-item label="LeetCode" prop="leetcode_handle">
          <el-input v-model="followingToAdd.leetcode_handle" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="follow">Add</el-button>
        </el-form-item>
      </el-form>
    </el-footer>
  </el-container>
</template>
<script>
import { AV, User } from "../logic/dbTable";

export default {
  data() {
    return {
      followingToAdd: {
        name: "",
        codeforces_handle: "",
        leetcode_handle: "",
      },
      tableData: [],
      rules: {
        name: [
          {
            required: true,
            message: "Please enter name",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    getUserList() {
      const query = new AV.Query("user");
      query.find().then((result) => {
        this.tableData = result.map((user) => {
          return user.toJSON();
        });
      });
    },
    async follow() {
      const user = new User();
      user.set("name", this.followingToAdd.name);
      user.set("codeforces_handle", this.followingToAdd.codeforces_handle);
      user.set("leetcode_handle", this.followingToAdd.leetcode_handle);
      await user.save();
      this.getUserList();
    },
    async unfollow(name) {
      const query = new AV.Query("user");
      query.equalTo("name", name);
      const user = await query.first();
      await user.destroy();
      this.getUserList();
    },
  },
  async created() {
    this.getUserList();
  },
};
</script>
